'use client';

import { useEffect, useState, memo, useMemo } from 'react';
import { usePathname } from 'next/navigation';
import { useLanguageStore } from '@/stores/languageStore';
import { Language, settings } from '@/config/settings';

interface LanguageSyncProviderProps {
  children: React.ReactNode;
  initialLanguage?: string;
}

/**
 * URL Í≤ΩÎ°úÏóêÏÑú Ïñ∏Ïñ¥ ÏΩîÎìú Ï∂îÏ∂ú
 */
function extractLanguageFromPath(pathname: string): Language {
  const segments = pathname.split('/').filter(Boolean);
  const firstSegment = segments[0];
  
  if (firstSegment && settings.languages.supported.includes(firstSegment as Language)) {
    return firstSegment as Language;
  }
  
  return settings.languages.default;
}

/**
 * Í∞úÎ∞úÏö© ÎîîÎ≤ÑÍ∑∏ Ï†ïÎ≥¥ Ïª¥Ìè¨ÎÑåÌä∏
 * hydration mismatchÎ•º Î∞©ÏßÄÌïòÍ∏∞ ÏúÑÌï¥ ÏôÑÏ†ÑÌûà Ï§ÄÎπÑÎêú ÌõÑÏóêÎßå ÌëúÏãú
 */
function DebugInfo({ 
  mounted, 
  isHydrated, 
  isLoading, 
  isTranslationReady, 
  currentLanguage 
}: {
  mounted: boolean;
  isHydrated: boolean;
  isLoading: boolean;
  isTranslationReady: boolean;
  currentLanguage: Language;
}) {
  if (process.env.NODE_ENV !== 'development') return null;

  return (
    <div className="fixed bottom-4 right-4 bg-black bg-opacity-75 text-white p-3 rounded text-xs z-50">
      <p>Mounted: {mounted ? '‚úÖ' : '‚ùå'}</p>
      <p>Hydrated: {isHydrated ? '‚úÖ' : '‚ùå'}</p>
      <p>Loading: {isLoading ? 'üîÑ' : '‚úÖ'}</p>
      <p>Translation Ready: {isTranslationReady ? '‚úÖ' : '‚ùå'}</p>
      {/* hydration ÏôÑÎ£å ÌõÑÏóêÎßå Ïñ∏Ïñ¥ ÌëúÏãúÌïòÏó¨ mismatch Î∞©ÏßÄ */}
      {mounted && isHydrated && (
        <p>Current Lang: {currentLanguage}</p>
      )}
    </div>
  );
}

/**
 * Ïñ∏Ïñ¥ ÎèôÍ∏∞Ìôî Provider - Î≤àÏó≠ ÎèôÍ∏∞ÌôîÎßå Îã¥Îãπ
 * Í∞Å ÌéòÏù¥ÏßÄÏùò Suspense fallbackÏù¥ Î°úÎî© UIÎ•º Ï≤òÎ¶¨ÌïòÎèÑÎ°ù Ìï®
 */
const LanguageSyncProviderComponent = memo(function LanguageSyncProviderInternal({ 
  children, 
  initialLanguage 
}: LanguageSyncProviderProps) {
  const pathname = usePathname();
  const { 
    isHydrated, 
    currentLanguage, 
    setHydrated, 
    syncLanguageWithPath,
    loadTranslations,
    isTranslationLoaded,
    translations,
    isLoading,
    setCurrentLang
  } = useLanguageStore();
  
  const [mounted, setMounted] = useState(false);

  // ÌòÑÏû¨ Ïñ∏Ïñ¥Ïùò Î≤àÏó≠Ïù¥ Ï§ÄÎπÑÎêòÏóàÎäîÏßÄ ÌôïÏù∏
  const isTranslationReady = useMemo(() => {
    return isTranslationLoaded[currentLanguage] && 
           translations[currentLanguage] && 
           Object.keys(translations[currentLanguage]).length > 0;
  }, [isTranslationLoaded, translations, currentLanguage]);

  // Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏ Í∞êÏßÄ
  useEffect(() => {
    setMounted(true);
  }, []);

  // ÏÑúÎ≤ÑÏóêÏÑú Ï†ÑÎã¨Îêú initialLanguageÎ°ú Ï¥àÍ∏∞Ìôî (hydration mismatch Î∞©ÏßÄ)
  useEffect(() => {
    if (mounted && !isHydrated && initialLanguage && initialLanguage !== currentLanguage) {
      // initialLanguageÍ∞Ä Ïú†Ìö®Ìïú LanguageÏù∏ÏßÄ Í≤ÄÏ¶ù
      const validLanguage = settings.languages.supported.includes(initialLanguage as Language) 
        ? initialLanguage as Language 
        : settings.languages.default;
      
      console.log(`üîÑ [LanguageSyncProvider] Initializing with server language: ${validLanguage}`);
      setCurrentLang(validLanguage);
    }
  }, [mounted, isHydrated, initialLanguage, currentLanguage, setCurrentLang]);

  // ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ hydration Ï≤òÎ¶¨
  useEffect(() => {
    if (mounted && !isHydrated) {
      setHydrated(true);
    }
  }, [mounted, isHydrated, setHydrated]);

  // Í≤ΩÎ°ú Î≥ÄÍ≤Ω Ïãú Ïñ∏Ïñ¥ ÎèôÍ∏∞Ìôî (hydration ÌõÑÏóêÎßå)
  useEffect(() => {
    if (mounted && isHydrated) {
      syncLanguageWithPath();
    }
  }, [mounted, isHydrated, pathname, syncLanguageWithPath]);

  // Î≤àÏó≠ Î°úÎî©
  useEffect(() => {
    if (mounted && isHydrated && !isTranslationReady && !isLoading) {
      loadTranslations(currentLanguage);
    }
  }, [mounted, isHydrated, isTranslationReady, isLoading, currentLanguage, loadTranslations]);

  // Ï§ÄÎπÑÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞ null Î∞òÌôò ‚Üí Í∞Å ÌéòÏù¥ÏßÄÏùò Suspense fallbackÏù¥ ÌëúÏãúÎê®
  if (!mounted || !isHydrated || isLoading || !isTranslationReady) {
    return (
      <DebugInfo 
        mounted={mounted}
        isHydrated={isHydrated}
        isLoading={isLoading}
        isTranslationReady={isTranslationReady}
        currentLanguage={currentLanguage}
      />
    );
  }

  // Ï§ÄÎπÑ ÏôÑÎ£å Ïãú children Î†åÎçîÎßÅ
  return (
    <>
      {children}
      <DebugInfo 
        mounted={mounted}
        isHydrated={isHydrated}
        isLoading={isLoading}
        isTranslationReady={isTranslationReady}
        currentLanguage={currentLanguage}
      />
    </>
  );
});

LanguageSyncProviderComponent.displayName = 'LanguageSyncProvider';

export { LanguageSyncProviderComponent as LanguageSyncProvider }; 